<template>
  <div class="app">
    <el-card class="box-card">
      <div class="">
            <el-button @click="addShow = true,title='',id=''" size="small"></el-button>
            <el-input style="width: 150px" placeholder="" v-model.trim="keyword" size="small"></el-input>
            <el-button type="primary" @click="getData(1)" size="small"></el-button>
            <el-button type="warning" @click="stockUpdate(0)" size="small">/</el-button>
            <el-button type="danger" @click="stockDelete(0)" size="small"></el-button>
            <el-button type="danger" @click="openStockShow = true" size="small"></el-button>
            <el-button type="danger" @click="updateName()" size="small" :loading="loading"></el-button>
            <el-button type="primary" @click="GetStock()" size="small" :loading="loading"></el-button>
      </div>
      <div class="title-red">
        <div><a href="http://data.eastmoney.com/xg/xg/default.html" target="view_window">http://data.eastmoney.com/xg/xg/default.html</a></div>
        <div></div>
        <div>300001SZ300001  600001SH600001</div>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card" v-loading="loading">
      <el-table
        :data="tableData"
        :height="tableHeight"
        style="width: 100%"
        stripe
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        border
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="stock_name" label=""></el-table-column>
        <el-table-column prop="code" label=""></el-table-column>
        <el-table-column prop="code_all" label=""></el-table-column>
        <el-table-column prop="jp" label=""></el-table-column>
        <el-table-column prop="open_time" label=""></el-table-column>
        <el-table-column sortable prop="is_open" label="">
          <template slot-scope="scope">
            <span :style="scope.row.is_open == 1?'color:#070':'color:#f00'">
               {{scope.row.is_open == 1?'':''}}
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="description" label="" width="240px">
          <template slot-scope="scope">
            <el-button @click="editStock(scope.row)" size="mini"></el-button>
            <el-button v-if="scope.row.is_open == 1" type="warning" @click="stockUpdate(scope.row.id)" size="mini">
            </el-button>
            <el-button v-if="scope.row.is_open != 1" type="success" @click="stockUpdate(scope.row.id)" size="mini">
            </el-button>
            <el-button type="danger" @click="stockDelete(scope.row.id)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="height: 10px"></div>
      <div class="tabListPage">
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="totalCount">
        </el-pagination>
      </div>
    </el-card>
    <el-dialog :title="title" :visible.sync="addShow" width="400px">
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="name"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="code"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="code_all"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="jp"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name"></div>
        <el-date-picker
          width="100%"
          v-model.trim="open_time"
          type="date"
          :default-date="open_time"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </div>
      <div class="password-inp">
        <div style="margin: 10px 0"></div>
        <el-radio v-model.trim="radio" label="1"></el-radio>
        <el-radio v-model.trim="radio" label="0"></el-radio>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addShow = false"> </el-button>
        <el-button type="primary" @click="addStock"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="openStockShow" width="400px">
      <div class="">
        <div class="password-name">,","</div>
        <el-input
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 4}"
          placeholder=""
          v-model.trim="textarea">
        </el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="openStockShow = false"> </el-button>
        <el-button type="success" @click="openStock(1)"></el-button>
        <el-button type="danger" @click="openStock(0)"></el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="newStock" width="800px">
      <div class="">
        <el-table
          :data="newData"
          style="width: 100%"
          stripe
          :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
          :header-cell-style="{'text-align':'center'}"
          border
        >
          <el-table-column prop="stock_name" label=""></el-table-column>
          <el-table-column prop="code" label=""></el-table-column>
          <el-table-column prop="code_all" label=""></el-table-column>
          <el-table-column prop="jp" label=""></el-table-column>
          <el-table-column prop="open_time" label=""></el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="newStock = false"></el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        newStock: false,
        tableHeight: 500,
        loading:false,
        textarea:'',
        openStockShow:false,
        title: '',
        id: '',
        keyword: '',
        addShow: false,
        name: '',
        code: '',
        code_all: '',
        jp: '',
        radio: '1',
        newData:[],
        open_time: '',
        // 
        tableData: [],
        // 
        currentPage: 1,
        // ()
        totalCount: 1,
        // 
        pageSizes: [20,50,150,300,1000],
        // 
        PageSize: 20,
      }
    },
    watch:{
      $route(){
        this.tableHeight = window.innerHeight-345;
        this.getData(this.PageSize, this.currentPage)
      }
    },
    created: function () {
      this.tableHeight = window.innerHeight-345;
      this.getData(this.PageSize, this.currentPage)
    },
    methods: {
      GetStock(){
        this.loading = true
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/newStockList', {
          data: this.data
        })
          .then(function (res) {
            _this.loading = false;
            _this.newData = res.data.data
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.newStock = true;
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
            }
          })
      },
      updateName(){
        this.loading = true
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/stockCheckSina', {
          data: this.data
        })
          .then(function (res) {
            _this.loading = false
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
            }
          })
      },
      // 
      getData(a) {
        if(a == 1){
          this.currentPage = 1
        }
        this.loading = true
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'limit': this.PageSize,
          'keyword': this.keyword
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/stockList', {
          data: this.data
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            _this.loading = false
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.tableData = res.data.data.data;
              _this.totalCount = res.data.data.total
            }
          })
      },
      openStock(a) {
        var _this = this
        this.$http.post('' + 'stock/admin/mulitStockOpen', {
          'k': localStorage['stock_token'],
          'code': this.textarea,
          'open': a,
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      stockUpdate(id) {
        if (id != 0) {
          this.id = id
        }
        var _this = this
        this.$http.post('' + 'stock/admin/stockUpdate', {
          'k': localStorage['stock_token'],
          'id': this.id
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      stockDelete(id) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.deleteStock(id)
        }).catch(() => {
        });
      },
      deleteStock(id) {
        if (id != 0) {
          this.id = id
        }
        var _this = this
        this.$http.post('' + 'stock/admin/stockDelete', {
          'k': localStorage['stock_token'],
          'id': this.id
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      editStock(data) {
        this.title = ''
        this.addShow = true
        this.id = data.id
        this.name = data.stock_name
        this.code = data.code
        this.code_all = data.code_all
        this.is_open = data.is_open
        this.jp = data.jp
        this.open_time = data.open_time
      },
      addStock() {
        if (this.id == ''){
          var url = 'stockAdd'
        } else {
          var url = 'stockEdit'
        }
        var _this = this
        this.$http.post('' + 'stock/admin/'+url, {
          'k': localStorage['stock_token'],
          'id': this.id,
          'stock_name': this.name,
          'code': this.code,
          'code_all': this.code_all,
          'is_open': this.radio,
          'jp': this.jp,
          'open_time': this.open_time,
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.addShow = false
              _this.getData()
            }
          })
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        var s = ''
        for (var i = 0; i < val.length; i++) {
          s += val[i].id + ','
        }
        this.id = s.slice(0, -1);
      },
      // 
      handleSizeChange(val) {
        // 
        this.PageSize = val
        // 
        this.getData()
        // 
        this.currentPage = 1
      },
      // 
      handleCurrentChange(val) {
        // 
        this.currentPage = val
        // 
        this.getData()
      },
    },
  }
</script>

<style scoped>
  .title-red{
    color: #f00;
    font-size: 15px;
    font-weight: bold;
    padding-top: 10px;
  }
  .password-inp {
    margin: 10px auto;
    max-width: 220px;
  }

  .password-name {
    font-size: 15px;
    padding: 5px;
  }
</style>



