<template>
  <div>
    <el-card class="box-card">
      <div class="">
          <el-button @click="newShow = true" size="small"></el-button>
          <el-input style="width: 150px" placeholder="" size="small" v-model.trim="keyword"></el-input>
          <el-button type="primary" size="small" @click="searchUser()"></el-button>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card">
      <el-table
        :data="tableData"
        style="width: 100%" stripe
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        border>
        <el-table-column prop="id" label="id"></el-table-column>
        <el-table-column prop="title" label=""></el-table-column>
        <el-table-column prop="status" label="">
          <template slot-scope="scope">
            {{scope.row.status == 1?'':''}}
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="" width="170px"></el-table-column>
        <el-table-column prop="update_time" label="" width="170px"></el-table-column>
        <el-table-column prop="description" label="" width="255px">
          <template slot-scope="scope">
            <el-button style="margin: 0 5px" type="default" size="mini" @click="Permissions(scope.row.id,scope.row.rules)"></el-button>
            <el-button style="margin: 0 5px" type="primary"
                       @click="editShow = true ,id = scope.row.id,title= scope.row.title" size="mini">
            </el-button>
            <el-button @click="deleteUser(scope.row.id)" style="margin: 0 5px" type="danger" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="height: 10px"></div>
      <div class="tabListPage">
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="totalCount">
        </el-pagination>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-dialog title="" :visible.sync="editShow" width="300px">
      <div class="password-inp">
        <el-input placeholder="" v-model.trim="title"></el-input>
      </div>
      <div class="password-inp">
        <div style="margin: 10px 0"></div>
        <el-radio v-model.trim="radio" label="1"></el-radio>
        <el-radio v-model.trim="radio" label="0"></el-radio>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editShow = false"> </el-button>
        <el-button type="primary" @click="editUser"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="newShow" width="300px">
      <div class="password-inp">
        <el-input placeholder="" v-model.trim="title"></el-input>
      </div>
      <div class="password-inp">
        <div style="margin: 10px 0"></div>
        <el-radio v-model.trim="radio" label="1"></el-radio>
        <el-radio v-model.trim="radio" label="0"></el-radio>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="newShow = false"> </el-button>
        <el-button type="primary" @click="newUser"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="PermissionsShow" width="500px">
      <div class="Permissions-title"></div>
        <el-checkbox-group v-model.trim="checkList">
          <el-checkbox label="/stockLists"></el-checkbox>
          <el-checkbox label="/holiday"></el-checkbox>
          <el-checkbox label="/moneyLists"></el-checkbox>
          <el-checkbox label="/deliveryOrder"></el-checkbox>
          <el-checkbox label="/stockHold"></el-checkbox>
          <el-checkbox label="/stockUser"></el-checkbox>
          <el-checkbox label="/entrustLists"></el-checkbox>
          <el-checkbox label="/sellYingkui"></el-checkbox>
          <el-checkbox label="/xrList"></el-checkbox>
          <el-checkbox label="/xrSerach"></el-checkbox>
          <el-checkbox label="/userExport"></el-checkbox>
        </el-checkbox-group>
      <div class="Permissions-title"></div>
      <el-checkbox-group v-model.trim="checkList">
        <el-checkbox label="/systemUser"></el-checkbox>
        <el-checkbox label="/systemAdmin"></el-checkbox>
        <el-checkbox label="/config"></el-checkbox>
      </el-checkbox-group>
      <div class="Permissions-title"></div>
      <el-checkbox-group v-model.trim="checkList">
        <el-checkbox label="/systemPic"></el-checkbox>
        <el-checkbox label="/sendMessage"></el-checkbox>
      </el-checkbox-group>
      <div slot="footer" class="dialog-footer">
        <el-button @click="PermissionsShow = false"> </el-button>
        <el-button type="primary" @click="PermissionsSub"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'index',
    data() {
      return {
        id: 0,
        checkList: [],
        radio: '1',
        title: '',
        keyword: '',
        editShow: false,
        newShow: false,
        PermissionsShow: false,
        // 
        tableData: [],
        // 
        currentPage: 1,
        // ()
        totalCount: 1,
        // 
        pageSizes: [20,50,150,300],
        // 
        PageSize: 20,
      }
    },
    mounted: function () {
      //console.log(window.location.pathname)
      var str = sessionStorage['menu']
      //console.log(str.indexOf(window.location.pathname) != -1 );
    },
    watch:{
      $route(){
        this.getData(this.PageSize, this.currentPage)
      }
    },
    methods: {
      // 
      getData() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'limit': this.PageSize,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/groups', {
          data: this.data
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.tableData = res.data.data.data;
              _this.totalCount = res.data.data.total
            }
          })
      },
      Permissions(id,rules){
        this.PermissionsShow = true
        this.id = id
        this.checkList = rules.split(",")
      },
      PermissionsSub(){
        var _this = this
        var s = ''
        for (var i = 0; i<this.checkList.length; i++){
          s += this.checkList[i]+','
        }
        this.$http.post('' + 'stock/sys/editRule', {
          'k': localStorage['stock_token'],
          'rules': s.slice(0,-1),
          'id':this.id
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      deleteUser(id) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.deleteLog(id)
        }).catch(() => {
        });
      },
      editUser() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'id': this.id,
          'title': this.title,
          'status': this.radio,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/groupsEdit', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      newUser() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'title': this.title,
          'status': this.radio,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/groupsAdd', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      searchUser() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'keyword': this.keyword,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/groups', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.tableData = res.data.data.data;
              _this.totalCount = res.data.data.total
            }
          })
      },
      deleteLog(id) {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'id': id
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/groupsDelete', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      // 
      handleSizeChange(val) {
        // 
        this.PageSize = val
        // 
        this.getData()
        // 
        this.currentPage = 1
      },
      // 
      handleCurrentChange(val) {
        // 
        this.currentPage = val
        // 
        this.getData()
      },
    },
    created: function () {
      this.getData(this.PageSize, this.currentPage)
    }
  }
</script>

<style scoped>
  .Permissions-title {
    font-size: 16px;
    padding-bottom: 20px;
  }
</style>



