<template>
  <div class="app">
    <el-card class="box-card" style="height: 110px;">
      <div style="height: 30px">
        <div style="float: left">
          <el-input placeholder="" v-model.trim="keyword" size="small" style="width: 150px"></el-input>
          <el-button type="primary" @click="getData(1)" size="small"></el-button>
          <el-button @click="addShow = true" type="primary" size="small">/</el-button>
          <el-button @click="inoutMore = true" type="success" size="small"></el-button>
          <el-button @click="inoutOut = true;remark=''" type="warning" size="small"></el-button>
          <el-button @click="inoutMoney = true" type="danger" size="small"></el-button>
          <el-button v-loading="loading" type="primary" @click="exportTable()" size="small"></el-button>
        </div>
        <div style="float: right">
          <span> &nbsp;</span>
          <el-input placeholder="" v-model.trim="ResetName" size="small" style="width: 150px"></el-input>
          <el-button type="primary" @click="searchReset()" size="small"></el-button>
        </div>
        <div style="clear: both"></div>
      </div>
      <div style="margin-top: 15px">
        <el-date-picker
          v-model.trim="start"
          type="date"
          size="small"
          value-format="yyyy-MM-dd"
          placeholder="" width="170px" style="width: 160px">
        </el-date-picker>
        <el-date-picker
          v-model.trim="end"
          type="date"
          size="small"
          value-format="yyyy-MM-dd"
          placeholder="" width="170px" style="width: 160px">
        </el-date-picker>
        <el-button type="primary" @click="getData()" size="small"></el-button>
      </div>
      <div style="clear: both"></div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card">
      <div class="inoutAll">
        <div class="inoutInfo">: {{inoutData.jrrj}}</div>
        <div class="inoutInfo">: {{inoutData.jrpz}}</div>
        <div class="inoutInfo">: {{inoutData.jrty}}</div>
        <div class="inoutInfo">: {{inoutData.jrzb}}</div>
      </div>
      <div style="height: 1px; background: #eee;margin: 10px 0"></div>
      <div class="inoutAll">
        <div class="inoutInfo">: {{inoutData.byrj}}</div>
        <div class="inoutInfo">: {{inoutData.bypz}}</div>
        <div class="inoutInfo">: {{inoutData.byty}}</div>
        <div class="inoutInfo">: {{inoutData.byzb}}</div>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card" v-loading="loading">
      <el-table
        id="table-data"
        :data="tableData"
        style="width: 100%"
        :height="tableHeight"
        stripe
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        border
      >
        <el-table-column prop="id" label="ID"></el-table-column>
        <el-table-column prop="sn" label=""></el-table-column>
        <el-table-column prop="user_name" label=""></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="tvalue" label="">
          <template slot-scope="scope">
            <span :style="scope.row.type == 0?'color:#f00':'color:#070'">
              {{scope.row.type == 0?'+'+scope.row.tvalue:'-'+scope.row.tvalue}}
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="addtime" label="" width="170px"></el-table-column>
        <el-table-column prop="checktime" label="" width="170px"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            {{scope.row.state == 1?'':''}}
          </template>
        </el-table-column>
        <el-table-column prop="remark" label=""></el-table-column>
        <el-table-column prop="money_type_str" label=""></el-table-column>
        <el-table-column prop="description" label="">
          <template slot-scope="scope">
            <el-button type="danger" @click="stockDelete(scope.row.id)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="height: 10px"></div>
      <div class="tabListPage">
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="totalCount">
        </el-pagination>
      </div>
    </el-card>
    <el-dialog title="/" :visible.sync="addShow" width="400px">
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="user_name"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="money"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="remark"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addShow = false"> </el-button>
        <el-button type="primary" @click="addStock(0)"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="inoutMore" width="400px">
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="user_name"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="money"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="remark"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="inoutMore = false"> </el-button>
        <el-button type="primary" @click="addMore" :loading="loading"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="inoutOut" width="400px">
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="user_name"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="money"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="remark" value=""></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="inoutOut = false"> </el-button>
        <el-button type="primary" @click="inoutGive()" :loading="loading"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="inoutMoney" width="400px">
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="user_name"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="money"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name"></div>
        <el-input placeholder="" v-model.trim="remark"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="inoutMoney = false"> </el-button>
        <el-button type="primary" @click="addOut" :loading="loading"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="reset" width="600px">
      <el-table
        :data="resetList"
        style="width: 100%"
        stripe
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        border
      >
        <el-table-column prop="user_name" label=""></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="create_time" label=""></el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>
<script>
  import fileSaver from 'file-saver'
  import xlsx from 'xlsx'
  export default {
    data() {
      return {
        reset:false,
        loading: false,
        id: '',
        start: '',
        end: '',
        keyword: '',
        ResetName:'',
        addShow: false,
        inoutMore: false,
        inoutOut: false,
        inoutMoney: false,
        user_name: '',
        resetList:'',
        money: '',
        remark: '',
        type: '0',
        options: [{
          value: '0',
          label: ''
        }],
        moneyoptions: [{
          value: '0',
          label: ''
        }, {
          value: '1',
          label: ''
        }],
        // 
        tableHeight: 500,
        tableData: [],
        inoutData: [],
        // 
        currentPage: 1,
        // ()
        totalCount: 1,
        // 
        pageSizes: [20, 50, 150, 300],
        // 
        PageSize: 20,
      }
    },
    watch: {
      $route() {
        this.tableHeight = window.innerHeight - 395;
        this.getData(this.PageSize, this.currentPage)
      }
    },
    created: function () {
      this.tableHeight = window.innerHeight - 395;
      this.getData(this.PageSize, this.currentPage)
    },
    methods: {
      // 
      getData(a) {
        if(a == 1){
          this.currentPage = 1
        }
        this.loading = true
        var _this = this
        this.$http.post('' + 'stock/admin/inoutList', {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'start': this.start,
          'end': this.end,
          'limit': this.PageSize,
          'keyword': this.keyword
        })
          .then(function (res) {
            _this.loading = false
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.inoutData = res.data.data;
              _this.tableData = res.data.data.list.data;
              _this.totalCount = res.data.data.list.total
            }
          })
      },
      searchReset(){
        this.reset = true
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'user_name': this.ResetName,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/searchReset', {
          data: this.data
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            _this.resetList = res.data.data
          })
      },
      stockDelete(id) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.deleteStock(id)
        }).catch(() => {
        });
      },
      deleteStock(id) {
        if (id != 0) {
          this.id = id
        }
        var _this = this
        this.$http.post('' + 'stock/admin/inoutCancel', {
          'k': localStorage['stock_token'],
          'id': this.id
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      //
      addStock(a) {
        this.loading = true
        var _this = this
        this.$http.post('' + 'stock/admin/inoutAdd', {
          'k': localStorage['stock_token'],
          'type': a,
          'moneytype': this.moneytype,
          'user_name': this.user_name,
          'multiple':this.moneytype,
          'money': this.money,
          'remark': this.remark,
        })
          .then(function (res) {
            _this.loading = false
            _this.user_name = ''
            _this.money = ''
            _this.remark = ''
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      inoutGive() {
        this.loading = true
        var _this = this
        this.$http.post('' + 'stock/admin/inoutGive', {
          'k': localStorage['stock_token'],
          'user_name': this.user_name,
          'money': this.money,
          'remark': this.remark,
        })
          .then(function (res) {
            _this.loading = false
            _this.user_name = ''
            _this.money = ''
            _this.remark = ''
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      //
      addMore() {
        this.loading = true
        var _this = this
        this.$http.post('' + 'stock/admin/inoutMore', {
          'k': localStorage['stock_token'],
          'user_name': this.user_name,
          'money': this.money,
          'remark': this.remark,
        })
          .then(function (res) {
            _this.loading = false
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      //
      addOut() {
        this.loading = true
        var _this = this
        this.$http.post('' + 'stock/admin/inoutOut', {
          'k': localStorage['stock_token'],
          'user_name': this.user_name,
          'money': this.money,
          'remark': this.remark,
        })
          .then(function (res) {
            _this.loading = false
            _this.user_name = ''
            _this.money = ''
            _this.remark = ''
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      // 
      handleSizeChange(val) {
        // 
        this.PageSize = val
        // 
        this.getData()
        // 
        this.currentPage = 1
      },
      // 
      handleCurrentChange(val) {
        // 
        this.currentPage = val
        // 
        this.getData()
      },
      exportData(){
        this.loading = true
        var _this = this
        if(_this.totalCount > 5000){
          _this.$message({
            message: '5000,',
            type: 'warning'
          });
          _this.loading = false
          return
        }
        this.$http.post('' + 'stock/admin/inoutList', {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'start': this.start,
          'end': this.end,
          'limit': 5000,
          'keyword': this.keyword
        })
          .then(function (res) {
            _this.loading = false
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.inoutData = res.data.data;
              _this.tableData = res.data.data.list.data;
              _this.totalCount = res.data.data.list.total
              if(_this.totalCount > 5000){
                _this.$message({
                  message: '5000,',
                  type: 'warning'
                });
                _this.loading = false
                return
              }
              setTimeout(function () {
                _this.exportTable()
              }, 3000)
            }
          })
      },
      //Excel
      exportTable() {
        /*  */
        var wb = xlsx.utils.table_to_book(document.querySelector("#table-data"));
        /*  */
        var wbout = xlsx.write(wb, {
          bookType: "xlsx",
          bookSST: true,
          type: "array"
        });
        var now = new Date();
        var year = now.getFullYear(); //
        var month = now.getMonth();//
        month = month + 1;
        var date = now.getDate();
        var xlname = year + '' + month + '' + date
        var name = window.location.host + xlname
        try {
          fileSaver.saveAs(
            new Blob([wbout], {type: "application/octet-stream"}),
            //
            name + ".xlsx"
          );
          this.loading = false
        } catch (e) {
          if (typeof console !== "undefined") {
            alert('')
          }
        }
        return wbout;
      }
    },
  }
</script>

<style scoped>
  .password-inp {
    margin: 10px auto;
    max-width: 220px;
  }

  .inoutAll {
    display: flex;
  }

  .inoutInfo {
    flex: 1;
  }

  .password-name {
    font-size: 15px;
    padding: 5px;
  }
</style>



