<script>
  export default {
    name: "index",
    data() {
      return {
        loading: false,
        configList: [{}],
        options: [{
          value: '0',
          label: ''
        }, {
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }],
        value: ''
      }
    },
    mounted: function () {
      this.getConfig()
    },
    watch: {
      $route() {
        this.getConfig()
      }
    },
    methods: {
      configSubmit() {
        this.loading = true
        this.configList.send_way = this.value
        var _this = this;
        this.$http.post('' + 'stock/admin/configsEdit', {
          'k': localStorage['stock_token'],
          'config': this.configList
        })
          .then(function (res) {
            _this.loading = false
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
            }
          })
      },
      getConfig() {
        var _this = this;
        var str = {
          'k': localStorage['stock_token'],
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/configs', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.configList = res.data.data;
              _this.value = _this.options[_this.configList.send_way].value
            }
          })
      },
    }
  }
</script>

