<template>
  <div>
    <el-card class="box-card">
      <div class="">
        <el-button @click="newShow = true" size="small"></el-button>
        <el-input placeholder="" style="width: 150px" size="small" v-model.trim="keyword"></el-input>
        <el-button type="primary" size="small" @click="searchUser(1)"></el-button>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card">
      <el-table
        :data="tableData"
        style="width: 100%" stripe
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        border>
        <el-table-column prop="id" label="id"></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="title" label=""></el-table-column>
        <el-table-column prop="loginnum" label=""></el-table-column>
        <el-table-column prop="status" label="">
          <template slot-scope="scope">
            {{scope.row.status == 1?'':''}}
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="" width="170px"></el-table-column>
        <el-table-column prop="update_time" label="" width="170px"></el-table-column>
        <el-table-column prop="description" label="" width="170px">
          <template slot-scope="scope">
            <el-button style="margin: 0 5px" type="primary" @click="editShowUser(scope.row)" size="mini"></el-button>
            <el-button @click="deleteUser(scope.row.id)" style="margin: 0 5px" type="danger" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="height: 10px"></div>
      <div class="tabListPage">
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="totalCount">
        </el-pagination>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-dialog title="" :visible.sync="editShow" width="300px">
      <div class="password-inp" style="width: 217px">
        <el-input placeholder="" v-model.trim="username"></el-input>
      </div>
      <div class="password-inp">
        <div style="margin: 10px 0"></div>
        <el-select v-model.trim="gg" placeholder="">
          <el-option
            v-for="item in options"
            :key="item.id"
            :label="item.title"
            :value="item.id">
          </el-option>
        </el-select>
      </div>
      <div class="password-inp" style="width: 217px">
        <div style="margin: 10px 0"></div>
        <el-input placeholder="" v-model.trim="editpassword" type="password"></el-input>
      </div>
      <div class="password-inp" style="width: 217px">
        <div style="margin: 10px 0"></div>
        <el-input placeholder="" v-model.trim="real_name"></el-input>
      </div>
      <div class="password-inp">
        <div style="margin: 10px 0"></div>
        <el-radio v-model.trim="radio" label="1"></el-radio>
        <el-radio v-model.trim="radio" label="0"></el-radio>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editShow = false"> </el-button>
        <el-button type="primary" @click="editUser"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="newShow" width="300px">
      <div class="password-inp" style="width: 217px">
        <el-input placeholder="" v-model.trim="title"></el-input>
      </div>
      <div class="password-inp">
        <div style="margin: 10px 0"></div>
        <el-select v-model.trim="id" placeholder="">
          <el-option
            v-for="item in options"
            :key="item.id"
            :label="item.title"
            :value="item.id">
          </el-option>
        </el-select>
      </div>
      <div class="password-inp" style="width: 217px">
        <div style="margin: 10px 0"></div>
        <el-input placeholder="" v-model.trim="password" type="password"></el-input>
      </div>
      <div class="password-inp" style="width: 217px">
        <div style="margin: 10px 0"></div>
        <el-input placeholder="" v-model.trim="real_name"></el-input>
      </div>
      <div class="password-inp">
        <div style="margin: 10px 0"></div>
        <el-radio v-model.trim="radio" label="1"></el-radio>
        <el-radio v-model.trim="radio" label="0"></el-radio>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="newShow = false"> </el-button>
        <el-button type="primary" @click="newUser"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'index',
    data() {
      return {
        id:'',
        gg:'',
        radio:'1',
        value:'',
        password:'',
        real_name:'',
        username:'',
        editpassword:'',
        title:'',
        keyword:'',
        options:[{}],
        editShow:false,
        newShow:false,
        // 
        tableData: [],
        // 
        currentPage: 1,
        // ()
        totalCount: 1,
        // 
        pageSizes: [20,50,150,300],
        // 
        PageSize: 20,
      }
    },
    watch:{
      $route(){
        this.getGroups()
        this.getData(this.PageSize, this.currentPage)
      }
    },
    methods: {
      // 
      getData() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'limit': this.PageSize,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/users', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.tableData = res.data.data.data;
              _this.totalCount = res.data.data.total
            }
          })
      },
      getGroups() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/groupsAll', {
          data: this.data
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.options = res.data.data
            }
          })
      },
      deleteUser(id){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.deleteLog(id)
        }).catch(() => {
        });
      },
      editShowUser(data){
        this.editShow = true,
        this.id = data.id,
        this.real_name = data.real_name
        this.username = data.username
      },
      editUser() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'id': this.id,
          'u': this.username,
          'p': this.editpassword,
          'r': this.real_name,
          'g': this.gg,
          's': this.radio,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/usersEdit', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      newUser() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'u': this.title,
          'p': this.password,
          'r': this.real_name,
          'g': this.id,
          's': this.radio,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/usersAdd', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      searchUser(){
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'keyword': this.keyword,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/users', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.tableData = res.data.data.data;
              _this.totalCount = res.data.data.total
            }
          })
      },
      deleteLog(id) {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'id': id
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/usersDelete', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      // 
      handleSizeChange(val) {
        // 
        this.PageSize = val
        // 
        this.getData()
        // 
        this.currentPage = 1
      },
      // 
      handleCurrentChange(val) {
        // 
        this.currentPage = val
        // 
        this.getData()
      },
    },
    created: function () {
      this.getGroups()
      this.getData(this.PageSize, this.currentPage)
    }
  }
</script>

<style scoped>
</style>



